#templazee-tab{
  max-width: 100% !important;
  #templazee-tab-content{

    .wp-block-templazee-tabs{
        display: none;
    }
    .wp-block-templazee-tabs[data-tab-active="active"]{
        display: block;
    }
    .wp-block-templazee-tabs[data-tab-active="inactive"]{
      display: none !important;
  }
    .wp-block-templazee-tabs:first-child{
        display: block;
    }

  }  

  #templazee-tab-item{
    ul{
      list-style: none;
      padding-left: 0px;
      
      display: flex;
      li{
        margin-right: 10px;
        padding: 10px 20px;
        border: 1px solid;
        cursor: pointer;
      }
    }
    
  }
}

#templazee-tab-item ul li:not(.inactive):first-child,
#templazee-tab-item ul li.active{
  background-color: #000;
  color: #fff;
}
